#modal-box .modal-layout{position:fixed;top:0;left:0;width:100vw;height:100vh;animation:drop-shadow 1s forwards;display:flex;justify-content:center;align-items:center;z-index:2}#modal-box .modal-layout .modal{scale:.8;background-color:#fff;position:sticky;padding:20px;border-radius:16px}@keyframes drop-shadow{from{background-color:rgba(0,0,0,0)}to{background-color:rgba(0,0,0,.5)}}.notify{position:fixed;right:30px;bottom:30px;display:flex;align-items:center;gap:12px;padding:16px 24px;border-radius:8px;box-shadow:0 4px 12px rgba(0,0,0,.15);color:#fff;max-width:320px;opacity:0;transform:translateY(20px);pointer-events:none;transition:all .3s ease;z-index:1000}.notify__icon{font-size:20px;flex-shrink:0}.notify__text{margin:0;font-size:14px;line-height:1.5}.notify__visible{opacity:1;transform:translateY(0);pointer-events:auto;animation:notify-hide 5s ease-in forwards}.notify__success{background-color:#4caf50;border-left:4px solid rgb(60.5577689243,139.4422310757,63.7450199203)}.notify__warning{background-color:#ffc107;color:#333;border-left:4px solid rgb(211,158.25,0)}.notify__error{background-color:#f44336;border-left:4px solid hsl(4.1052631579,89.6226415094%,48.431372549%)}.notify:hover{animation-play-state:paused}@keyframes notify-hide{0%{opacity:0;transform:translateY(20px)}10%{opacity:1;transform:translateY(0)}90%{opacity:1;transform:translateY(0)}100%{opacity:0;transform:translateY(-10px)}}.footer{background-color:#2c3e50;color:#ecf0f1;padding:3rem 0 0;position:relative;overflow:hidden}.footer__content{max-width:1200px;margin:0 auto;padding:0 2rem;display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));gap:2rem}.footer__section{margin-bottom:2rem;position:relative;z-index:2}.footer__title{color:#f5d04e;font-size:1.3rem;margin-bottom:1.5rem;position:relative;display:inline-block}.footer__title::after{content:"";position:absolute;bottom:-8px;left:0;width:50px;height:3px;background-color:#f5d04e;transition:width .3s ease}.footer__title:hover::after{width:80px}.footer__text{line-height:1.6;margin-bottom:1rem}.footer__link{display:block;color:#ecf0f1;margin-bottom:.8rem;text-decoration:none;transition:all .3s ease;position:relative;padding-left:20px}.footer__link::before{content:"→";position:absolute;left:0;color:#f5d04e;transition:transform .3s ease}.footer__link:hover{color:#f5d04e;transform:translateX(5px)}.footer__link:hover::before{transform:translateX(3px)}.footer__social{display:flex;flex-direction:column;gap:.8rem}.footer__social-link{color:#ecf0f1;text-decoration:none;transition:all .3s ease;position:relative;padding-left:25px}.footer__social-link::before{content:"";position:absolute;left:0;top:50%;transform:translateY(-50%);width:15px;height:15px;background-color:#f5d04e;border-radius:3px;transition:all .3s ease}.footer__social-link:hover{color:#f5d04e;transform:translateX(5px)}.footer__social-link:hover::before{transform:translate(5px, -50%);background-color:#ecf0f1}.footer__bottom{background-color:rgba(0,0,0,.2);padding:1.5rem 0;text-align:center;margin-top:2rem;position:relative;z-index:2}.footer__copyright{margin:0;font-size:.9rem;color:#bdc3c7}.footer::before{content:"";position:absolute;bottom:0;right:0;width:300px;height:300px;background:radial-gradient(circle, rgba(245, 208, 78, 0.1) 0%, transparent 70%);z-index:1}@media(max-width: 768px){.footer__content{grid-template-columns:1fr;gap:1.5rem}.footer__section{text-align:center}.footer__title::after{left:50%;transform:translateX(-50%)}}